.sortListContainer{
  width: 170px;
  box-sizing: border-box;
  border: 1px solid $bg-color-gray3;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
  padding: 5px 7px 5px 7px;
}

.sortListContainer>li{
  list-style: none;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 3px;
}

.sortListContainer>li:last-child{
  margin-bottom: 0;
} 