.mainBoard{
  font-size: 14px;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  margin-top: 20px;
  display: grid;
  width: 1280px;
  grid-template: 
  'install  error' 225px
  'time     time'  303px
  / 730px 1fr;
  row-gap: 15px;
  column-gap: 40px;
}

@mixin BorderGrayDiv {
  box-sizing: border-box;
  border: 1px solid #D9D9D9;
}

.currentInstall{
  @include BorderGrayDiv;
  grid-area: install;
  display: flex;
  flex-direction: column;
}

.currentError{
  @include BorderGrayDiv;
  grid-area: error;
  display: flex;
  flex-direction: column;
}

.usage{
  @include BorderGrayDiv;
  grid-area: time;
  display: grid;
  grid-template-rows: 40px 1fr;
}

.installDisplay{
  display: flex;
  height: 100%;
  padding-left: 18px;
  padding-right: 10px;
}

.installDisplayBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 234px;
}

.installDisplayBox:nth-child(3)>.installNumber{
  color: $brand-color-yellow;
}

.installNumber{
  font-family: 'Roboto', sans-serif;
  font-size: 35px;
  font-weight: 700;
  margin-top: 52px;
  margin-bottom: 22px;
}

.installText{
  font-size: 14px;
  font-weight: 400;
  margin-bottom: 43px;
}

.errorDisplay{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 140px;
  justify-content: center;
  column-gap: 19px;
  height: 100%;
  padding-left: 19px;
  padding-right: 19px;
  padding-top: 21px;
  padding-bottom: 23px;
}

.errorDisplayBox{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  color: $font-color-white;
  &:nth-child(1), &:nth-child(2){
    background-color: $bg-color-pink;
  }

  &:nth-child(3){
    background-color: $bg-color-gray1;
  }
}

.errorNumber{
  font-family: 'Roboto', sans-serif;
  font-size: 35px;
  font-weight: 700;
  margin-top: 33px;
  margin-bottom: 20px;
}

.errorText{
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 21px;
}