.detailStation-container {
  display: flex;
  flex-direction: column;
  width: 1280px;
  height: 815px;
  border: 1px solid $line-color-strong;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
  padding: 20px;
}

.detailStation-container .stationInfo-container,
.detailStation-container .stationType-container,
.detailStation-container .stationPort-container {
  border: 1px solid $line-color-intermediate;
}

.detailStation-table {
  table-layout: fixed;
  border-collapse: collapse;
  border-spacing: 0;
}

.detailStation-table td {
  padding-left: 20px;
  font-size: 13px;
  color: $font-color-gray1;
  padding-right: 12px;
  box-sizing: border-box;
}

.detailStation-table thead td {
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: $font-color-gray1;
  padding-top: 15px;
}

.detailStation-table tbody td {
  padding-top: 15px;
  padding-bottom: 15px;
  font-weight: 400;
  input {
    box-sizing: border-box;
    border: 1px solid $line-color-strong;
    border-radius: 5px;
    height: 27px;
    font-family: "Noto Sans KR", sans-serif;
    font-size: 13px;
    font-weight: 400;
    color: $font-color-gray1;
    padding: 4px 7px;
    width: 100%;
    word-spacing: -1px;
    letter-spacing: -0.2px;
  }
}

.detailStation-table tbody td.active {
  padding-left: 12px;
  padding-right: 12px;
  padding-bottom: 11px;
  padding-top: 11px;
}

.detailStation-table tbody td.active:nth-child(5) {
  padding-left: 20px;
}

.detailStation-table td:nth-child(1) {
  width: 151px;
}

.detailStation-table td:nth-child(2) {
  width: 234px;
}

.detailStation-table td:nth-child(3) {
  width: 281px;
}

.detailStation-table td:nth-child(4) {
  width: 153px;
}

.detailStation-table td:nth-child(5) {
  width: 157px;
}

.detailStation-table tbody td:nth-child(5) {
  color: $font-color-black;
  font-weight: 700;
}

.detailStation-table td:nth-child(6) {
  width: 146px;
}

.detailStation-table td:nth-child(7) {
  width: 114px;
}

.tiny-button {
  display: flex;
  border-radius: 5px;
  border: 1px solid $line-color-strong;
  margin-right: 4px;
  font-size: 10px;
  width: 30px;
  height: 19px;
  align-items: center;
  justify-content: center;
  &:nth-child(1) {
    color: $font-color-gray1;
    font-weight: 700;
  }
  &:nth-child(2) {
    color: $font-color-gray2;
    font-weight: 500;
  }
  span {
    position: relative;
    top: -1px;
  }
}

.stationType-table tr td:nth-child(1) {
  width: 100px;
}

.stationType-table tr td:nth-child(2) {
  padding-left: 70px;
  width: 200px;
}

.stationType-table tr td:nth-child(3) {
  padding-left: 70px;
  width: 200px;
}

.stationType-table tbody td.active:nth-child(2) {
  padding-left: 62px;
}

.stationType-table tbody td.active:nth-child(3) {
  padding-left: 62px;
}

.port-table tbody td {
  padding-top: 10px;
  padding-bottom: 0;
}

.port-table tbody tr:last-child td {
  padding-bottom: 12px;
}

.port-table tbody td.active {
  padding-top: 8px;
  padding-bottom: 0;
}

.port-table td:nth-child(2) {
  width: 154px;
}

.port-table td:nth-child(3) {
  padding-left: 66px;
  width: 190px;
}

.port-table td.active:nth-child(3) {
  padding-left: 58px;
}
