@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;400;500;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

/*
font-family: 'Roboto', sans-serif;
font-family: 'Noto Sans KR', sans-serif;
*/


/* font-weight 
  100: Thin
  300: Light
  400: Regular
  500: Medium
  700: Bold
  900: Black
*/

$brand-color-yellow: #FFCE00;
$brand-color-orange: #FF9700;
$brand-color-black: #231815;

$bg-color-gray1: #252525;
$bg-color-gray2: #6B6B6B;
$bg-color-gray3: #D9D9D9;
$bg-color-gray4: #F3F3F3;
$bg-color-pink: #FF5050;

$line-color-strong: #252525;
$line-color-intermediate: #D9D9D9; 
$line-color-weak: #F3F3F3;

$font-color-black: #000000;
$font-color-gray1: #6B6B6B;
$font-color-gray2: #AAAAAA;
$font-color-white: #FFFFFF;

$ui-color-gray: #252525;
$ui-color-red: #FF0000;

html{
  height: 100%;
}

body{
  height: 100%;
  padding: 0;
  margin: 0;
  font-family: 'Noto Sans KR', sans-serif;
  word-spacing: -1px;
  letter-spacing: -0.2px;
  box-sizing: border-box;
} 

ul{
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#root{
  display: grid;
  height: 100%;
  grid-template-rows: 64px 73px 1fr;
  justify-items: center;
}