.search{
  display: flex;
  align-items: center;
  width: 260px;
  height: 25px;
  border: 1px solid $bg-color-gray3;
  border-radius: 5px;
  padding: 0 5px;
  box-sizing: border-box;
}

.search>input{
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: $font-color-gray1;
  border: none;
  outline: none;
  margin-left: 17px;
}

.search>input:nth-child(2)::placeholder{
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: $font-color-gray2;
}