.drop-box {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 1em;
  > .head {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    > div {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      > img {
        margin-right: 5px;
      }
    }
    > img {
      margin-left: auto;
    }
  }
  > .body {
    position: absolute;
    z-index: 1;
    div {
      display: flex;
      align-items: center;
    }
  }
}
