.radio-container input{
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.radio-container input:hover + label::before{
  border-color: $line-color-strong;
  background-color: #f2f2f2;
}

.radio-container input:active + label::before{
  transition-duration: 0s;
}

.radio-container input:checked + label::before{
  animation: borderscale 200ms ease-in;
  background: $bg-color-gray1;
}

.radio-container input:checked + label::after{
  content: '';
  transform: rotate(-45deg) scale(1);
  transition: transform 200ms ease-out;
}

.radio-container label{
  position: relative;
  padding-left: 20px;
  padding-right: 10px;
  vertical-align: middle;
  user-select: none;
  cursor: pointer;
}

.radio-container label::before{
  content: '';
  position: absolute;
  top: 60%;
  left: 0;
  width: 12px;
  height: 12px;
  margin-top: -9px;
  border: 2px solid $line-color-strong;
  text-align: center;
  transition: all 0.4s ease;
  border-radius: 3px;
}

.radio-container label::after{
  box-sizing: content-box;
  position: absolute;
  content: '';
  background-color: transparent;
  top: 60%;
  left: 3px;
  width: 9px;
  height: 3px;
  margin-top: -4px;
  border-style: solid;
  border-color: #ffffff;
  border-width: 0 0 2px 2px;
  border-image: none;
  transform: rotate(-45deg) scale(0);
  transform-origin: 50%;
  transition: transform 200ms ease-out;
}

@keyframes borderscale{
  50%{
    box-shadow: 0 0 0 2px $line-color-strong;
  }
}

.boxByRadio-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 5px;
  margin-bottom: 23px;
}

.boxByRadio-container input{
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 12px;
  font-weight: 400;
  color: $font-color-black;
}

.boxByRadio-container input:disabled{
  background-color: $bg-color-gray4;
}