.managementStation_table {
  width: 1280px;
  box-sizing: border-box;
  border: 1.3px solid $line-color-intermediate;
  border-collapse: collapse;
}

.managementStation_table tr {
  box-sizing: border-box;
  border-bottom: 1px solid $bg-color-gray4;
}

.managementStation_table thead tr {
  height: 40px;
  background-color: $bg-color-gray4;
  border-bottom: 1.3px solid $line-color-intermediate;
}

.managementStation_table tbody tr {
  height: 31px;
}

.managementStation_table tbody tr:not(.empty):hover {
  background-color: rgba(255, 206, 0, 0.15);
  cursor: pointer;
}

.managementStation_table th {
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 500;
  font-size: 14px;
}

.managementStation_table td {
  box-sizing: border-box;
  font-family: "Noto Sans KR", sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: $font-color-gray1;
}

.managementStation_table th:nth-child(1),
.managementStation_table td:nth-child(1) {
  text-align: start;
  padding-left: 20px;
}

.managementStation_table th:nth-child(2),
.managementStation_table td:nth-child(2) {
  text-align: start;
  padding-left: 20px;
}

.managementStation_table th:nth-child(3),
.managementStation_table td:nth-child(3) {
  text-align: start;
  padding-left: 20px;
}

.managementStation_table th:nth-child(4),
.managementStation_table td:nth-child(4) {
  text-align: start;
}

.managementStation_table th:nth-child(5),
.managementStation_table td:nth-child(5) {
  text-align: start;
}

.managementStation_table th:nth-child(6),
.managementStation_table td:nth-child(6) {
  text-align: start;
  padding-left: 66px;
}

.managementStation_table th:nth-child(7),
.managementStation_table td:nth-child(7) {
  text-align: start;
  padding-left: 20px;
}
