.addStation-container{
  display: grid;
  grid-template-rows: 50px 1fr;
  width: 439px;
  height: 714px;
  border: 1px solid $ui-color-gray;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.4);
  box-sizing: border-box;
}

.addStation-title{
  display: flex;
  align-items: center;
  background-color: $brand-color-yellow;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 700;
  font-size: 16px;
  padding-left: 29px;
  box-sizing: border-box;
}

.addStation-form{
  padding: 20px 29px;
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
}

.addStation-form>div{
  width: 100%;
}

.addStation-form::-webkit-scrollbar{
  width: 4px;
}

.addStation-form::-webkit-scrollbar-track{
  background-color: transparent;
}

.addStation-form::-webkit-scrollbar-thumb{
  border-radius: 5px;
  background-color: $bg-color-gray3;
}

.addStation-form::-webkit-search-cancel-button{
  width: 0;
  height: 0;
}

.addStation-container label{
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  font-size: 12px;
  display: block;
  margin-bottom: 8px;
}

.addStation-container .longInput{
  box-sizing: border-box;
  width: 100%;
  height: 30px;
  border: 1px solid $line-color-intermediate;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 12px;
  padding: 0 8px;
  margin-bottom: 20px;
  flex-shrink: 0;
}

.addStation-container .shortInput{
  box-sizing: border-box;
  width: 100%;
  height: 30px;
  border: 1px solid $line-color-intermediate;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 400;
  font-size: 12px;
  padding: 0 8px;
}