.bgGrayDiv{
  display: flex;
  align-items: center;
  padding-left: 20px;
  box-sizing: border-box;
  border-bottom: 1px solid #D9D9D9;
  background-color: #F3F3F3;
  height: 40px;
  flex-shrink: 0;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 500;
  color: $font-color-black;
}

.editIcon, .setIcon{
  cursor: pointer;
  margin-left: auto;
}