.chargeLog_table{
  box-sizing: border-box;
  border: 1.3px solid $line-color-intermediate;
  border-collapse: collapse;
}

.chargeLog_table tr{
  box-sizing: border-box;
  border-bottom: 1px solid $bg-color-gray4;
}

.chargeLog_table thead tr{
  height: 40px;
  background-color: $bg-color-gray4;
  border-bottom: 1.3px solid $line-color-intermediate;
}

.chargeLog_table tbody tr{
  height: 30px;
}

.chargeLog_table th{
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  font-size: 14px;
}

.chargeLog_table td{
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 13px;
  color: $font-color-gray1;
}

.chargeLog_table th:nth-child(1), .chargeLog_table td:nth-child(1){
  text-align: start;
  width: 179px;
  padding-left: 20px;
}

.chargeLog_table th:nth-child(2), .chargeLog_table td:nth-child(2){
  text-align: start;
  width: 242px;
  padding-left: 20px;
}

.chargeLog_table th:nth-child(3), .chargeLog_table td:nth-child(3){
  text-align: start;
  width: 182px;
  padding-left: 20px;
}

.chargeLog_table th:nth-child(4), .chargeLog_table td:nth-child(4){
  text-align: center;
  width: 150px;
}

.chargeLog_table th:nth-child(5), .chargeLog_table td:nth-child(5){
  text-align: center;
  width: 150px;
}

.chargeLog_table th:nth-child(6), .chargeLog_table td:nth-child(6){
  text-align: start;
  width: 213px;
  padding-left: 66px;
}

.chargeLog_table th:nth-child(7), .chargeLog_table td:nth-child(7){
  text-align: start;
  width: 163px;
  padding-left: 20px;
}

.pagination{
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination li{
  list-style: none;
  width: 27px;
  height: 27px;
  text-align: center;
  cursor: pointer;
  color: $font-color-gray1;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.8;
}

.pagination .active{
  color: #F6F6F6;
  background-color: $bg-color-gray1;
}

.pagination button{
  width: 22px;
  height: 21px;
  background: transparent;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.pagination button:hover{
  background: $bg-color-gray4;
}

.prev{
  margin-right: 10px;
}

.next{
  margin-left: 10px;
}