.portInfo-container {
  display: flex;
  flex-direction: column;
}

.portInfoButton-container {
  display: flex;
  margin-bottom: 10px;
  > .drop-box {
    width: 100px;
  }
}

.portInfoButton-container img {
  cursor: pointer;
}

.portInfoButton-container img:nth-child(1) {
  margin-right: 5px;
}

.portList-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 5px;
}

.portNumber {
  background-color: $bg-color-gray4;
}
