.sortSelect{
  display: flex;
  align-items: center;
  width: 170px;
  height: 25px;
  border: 1px solid $bg-color-gray3;
  border-radius: 5px;
  padding: 0 6px 0 7px;
  box-sizing: border-box;
}

.sortSelect>div{
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: $font-color-gray1;
}

.sortSelect>img{
  width: 6px;
  height: 12px;
  margin-left: auto;
}