@import "./NavUlLi";

.subNav{
  position: relative;
  display: flex; 
  height: 31px;
  width: 100%;
  background-color: #FFFFFF;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
  justify-content: center;
  @include NavUlLi(1280px, $font-color-gray2, $font-color-black, #FFFFFF);
  li{
    position: relative;
    &:hover{
      div{
        background-color: $brand-color-yellow;
      }
    }
    div{
      position: absolute;
      bottom: -2px;
      width: 100%;
      height: 3px;
    }
  }
  >.ghost{
    position: absolute;
    border-bottom: 4px solid gold;
    transition: left ease 300ms;
  }
  a{
    text-decoration: none;
    font-family: 'Noto Sans KR', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: $font-color-gray1;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
    &:visited{
      color: inherit;
    }
    &.active{
      font-weight: 500;
      color: $font-color-black;
    }
    &:hover{
      color: $font-color-black;
      font-weight: 500;
    }
  }
}