#map > div:nth-child(3) {
  display: none;
}

@mixin container-default {
  display: flex;
  align-items: center;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 14px;
  font-weight: 400;
  color: $font-color-black;
}

.yellowBox-container {
  @include container-default;
}

.redBox-container {
  @include container-default;
}

.blackBox-container {
  @include container-default;
}

.yellowBox {
  width: 7px;
  height: 14px;
  background-color: $brand-color-yellow;
  margin-right: 7px;
}

.redBox {
  width: 7px;
  height: 14px;
  background-color: $ui-color-red;
  margin-right: 7px;
}

.blackBox {
  width: 7px;
  height: 14px;
  background-color: $ui-color-gray;
  margin-right: 7px;
}

.station {
  transform-origin: bottom center;
  transition: all 1s;
}

.station.active {
  transform: scale(1.4);
  filter: drop-shadow(0px 5px 3px rgba(0, 0, 0, 0.4));
}
