.deleteButton{
  width: 59px;
  height: 25px;
  color: $font-color-white;
  background-color: $bg-color-gray1;
  border-radius: 5px;
  box-sizing: border-box;
  outline: none;
  border: none;
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  margin-top: auto;
}

.deleteButton>p{
  position: relative;
  margin: 0;
  top: -1px;
}