.chargeNumber-container{
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
}

.chargeNumber{
  font-family: 'Roboto', sans-serif;
  color: $brand-color-yellow;
  font-size: 35px;
  font-weight: 700;
}

.chargeNumber-container>div:nth-child(2){
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 14px;
  color: $font-color-black;
}