.chargeLog_mainBoard{
  width: 1280px;
  display: flex;
  flex-direction: column;
}

.chargeLog_topBoard{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 15px;
  margin-top: 20px;
  margin-bottom: 30px;
}

.chargeLog_searchContainer{
  display: flex;
  align-items: center;
  margin-bottom: 9px; 
}

.chargeLog_sortSelectWrapper{
  margin-right: 30px;
}

.chargeLog_searchWrapper{
  margin-right: 5px;
}

.chargeLog_calendarIconWrapper{
  position: relative;
  top: -1px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 6px;
}

/* twoLineContainer */
.currentCharge{
  box-sizing: border-box;
  border: 1px solid $bg-color-gray3;
  display: flex;
  flex-direction: column;
  width: 226px;
  height: 302px;
}

.totalUse{
  box-sizing: border-box;
  border: 1px solid $bg-color-gray3;
  display: flex;
  flex-direction: column;
  width: 246px;
  height: 303px;
}

.totalChargeTime{
  box-sizing: border-box;
  border: 1px solid $bg-color-gray3;
  display: flex;
  flex-direction: column;
  width: 246px;
  height: 303px;
}

.statistics{
  box-sizing: border-box;
  border: 1px solid $bg-color-gray3;
  width: 517px;
  height: 303px;
}