.loginBox {
  display: flex;
  flex-direction: column;
  font-family: "Noto Sans KR", sans-serif;
  input {
    height: 50px;
    box-sizing: border-box;
    color: $font-color-black;
    border: {
      width: 1px;
      style: solid;
      radius: 5px;
      color: $line-color-intermediate;
    }
    margin-bottom: 10px;
    padding: 15px 16px;
    font: {
      family: inherit;
      weight: 500;
      style: normal;
      size: 14px;
    }
    &::placeholder {
      font-weight: 400;
      color: $font-color-gray2;
    }
    &:focus {
      border-color: $brand-color-yellow;
    }
    outline: none;
  }
  .messageBox {
    display: flex;
    color: $ui-color-red;
    font-size: 13px;
    height: 19px;
    margin-top: 6px;
    margin-bottom: 4px;
    align-items: center;
    opacity: 0;
    transition: all 1s ease;
  }
  .pullDown {
    opacity: 1;
  }
  button {
    cursor: pointer;
    height: 50px;
    box-sizing: border-box;
    background-color: $brand-color-yellow;
    border: {
      width: 0;
      radius: 5px;
    }
    font: {
      family: inherit;
      size: 16px;
      weight: 400;
    }
    margin-top: 10px;
    transition: all 0.4s ease;
    outline: none;
    position: relative;
    top: -27px;
  }
  .active {
    transform: translateY(25px);
  }
}

.loginQuestion-container {
  margin-left: auto;
  width: 91px;
  height: 25px;
  display: flex;
  align-items: center;
  color: $font-color-black;
  border: 1px solid $line-color-intermediate;
  border-radius: 3px;
  font-size: 14px;
  font-weight: 400px;
}

.head {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.head-text {
  font: {
    size: 35px;
    family: "Montserrat", sans-serif;
    weight: 900;
    style: normal;
  }
  margin: {
    top: 38.5px;
    bottom: 25px;
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
}
