.controlStation_table{
  box-sizing: border-box;
  border: 1.3px solid $line-color-intermediate;
  border-right: 1px solid $line-color-intermediate;
  border-left: 1px solid $line-color-intermediate;
  border-collapse: collapse;
}

.controlStation_table tr{
  box-sizing: border-box;
  border-bottom: 1px solid $bg-color-gray4;
}

.controlStation_table thead tr{
  height: 40px;
  background-color: $bg-color-gray4;
  border-bottom: 1.3px solid $line-color-intermediate;
}

.controlStation_table tbody tr{
  height: 31px;
}

.controlStation_table tbody tr:not(.empty):hover{
  background-color: rgba(255, 206, 0, 0.15);
  cursor: pointer;
}

.controlStation_table th{
  box-sizing: border-box;
  font-family: 'Noto Sans KR', sans-serif;
  font-weight: 500;
  font-size: 14px;
}

.controlStation_table td{
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 14px;
  color: $font-color-gray1;
}

.controlStation_table th:nth-child(1), .controlStation_table td:nth-child(1){
  text-align: start;
  width: 199px;
  padding-left: 20px;
}

.controlStation_table th:nth-child(2), .controlStation_table td:nth-child(2){
  text-align: start;
  width: 296px;
  padding-left: 20px;
}

.controlStation_table th:nth-child(3), .controlStation_table td:nth-child(3){
  text-align: start;
  width: 386px;
  padding-left: 20px;
}

.controlStation_table th:nth-child(4), .controlStation_table td:nth-child(4){
  text-align: start;
  width: 186px;
  padding-left: 20px;
}

.controlStation_table th:nth-child(5), .controlStation_table td:nth-child(5){
  text-align: start;
  width: 212px;
  padding-left: 20px;
}

#stateSelect:hover{
  cursor: pointer;
  background-color: rgb(207, 207, 207);
}