.userBox {
  display: flex;
  box-sizing: border-box;
  margin-left: auto;
  align-items: center;
  width: auto;
  height: 26px;
  padding-left: 7.1px;
  padding-right: 18px;
  border-radius: 6px;
  border: 1px solid $line-color-intermediate;
}

.currentUser {
  position: relative;
  bottom: 1px;
  margin-left: 4px;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 12px;
  font-weight: 400;
  color: $font-color-gray1;
}
